.upload_file_top {
    display: flex;
    justify-content: space-between;
}
.fileXlsx {
    margin-left: 0.3rem;
    font-family: sans-serif;
}
.upload_file_top button {
    float: right;
    background-color: #e74c3c !important;
    border: #e74c3c;
}
.actual-btn,
.upload_file_top button {
    background-color: #2c3e50;
    color: white;
    padding: 0.5rem;
    font-family: sans-serif;
    border-radius: 0.3rem;
    cursor: pointer;
    margin-top: 1rem;
}
.active-sheet {
    background-color: #34495e;
    color: white;
    padding: 5px;
    font-family: sans-serif;
    border-radius: 0.3rem;
    cursor: pointer;
    margin-top: 1rem;
}
.upload_file_content .error_duplicate p{
    color: red;
}